/* =========== GENEL =========== */

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    sans-serif;
  background: #f5f7fb;
  color: #222;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 12px 12px 40px;
}

/* =========== HEADER =========== */

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  padding: 12px 16px;
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 4px 16px rgba(15, 23, 42, 0.08);
  margin-bottom: 16px;
}

.header-left h1 {
  margin: 0;
  font-size: 1.4rem;
}

.header-left .muted {
  font-size: 0.9rem;
  color: #6b7280;
}

 
/* =========== BUTONLAR =========== */

.btn {
  border: none;
  border-radius: 999px;
  padding: 6px 14px;
  font-size: 0.9rem;
  cursor: pointer;
  background: #e5e7eb;
  color: #111827;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  transition: background 0.15s ease, box-shadow 0.15s ease, transform 0.05s;
}

.btn:hover {
  background: #d1d5db;
}

.btn:active {
  transform: translateY(1px);
}

.btn.primary {
  background: #4f46e5;
  color: #ffffff;
}

.btn.primary:hover {
  background: #4338ca;
}

.btn.danger {
  background: #ef4444;
  color: #ffffff;
}

.btn.danger:hover {
  background: #dc2626;
}

.btn.icon-btn .btn-icon {
  font-size: 1rem;
}

 

/* =========== KART, FORM, METƒ∞N =========== */

.card {
  background: #ffffff;
  border-radius: 16px;
  padding: 18px 20px;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.06);
  margin-bottom: 16px;
}

/* Bakƒ±m Hatƒ±rlatmalarƒ± sayfasƒ± i√ßin HR ayracƒ± */
hr {
  border: none;
  height: 2px;
  background: linear-gradient(to right, transparent, #e5e7eb 20%, #e5e7eb 80%, transparent);
  margin: 32px 0;
}

.form-card label {
  display: block;
  font-size: 0.85rem;
  margin-bottom: 8px;
}

.form-card input[type='text'],
.form-card input[type='number'],
.form-card input[type='password'],
.form-card input[type='file'],
.form-card textarea,
.month-select,
select {
  width: 100%;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  padding: 6px 10px;
  font-size: 0.9rem;
  margin-top: 4px;
  outline: none;
  transition: border 0.15s ease, box-shadow 0.15s ease;
}

.form-card input:focus,
.form-card textarea:focus,
.month-select:focus,
select:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 0 1px rgba(79, 70, 229, 0.25);
}

textarea {
  resize: vertical;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 12px;
}

.error-text {
  color: #b91c1c;
  font-size: 0.85rem;
  margin-top: 4px;
}

 

/* =========== LOGIN =========== */

.auth-container {
  max-width: 360px;
  margin: 80px auto;
  text-align: center;
}

.auth-container h2 {
  margin-bottom: 16px;
}

/* =========== TABLAR =========== */

.tabs {
  display: flex;
  gap: 8px;
  margin-bottom: 12px;
  border-bottom: 1px solid #e5e7eb;
}

.tab {
  border: none;
  background: transparent;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 0.9rem;
  border-radius: 999px 999px 0 0;
  color: #6b7280;
}

.tab.active {
  background: #ffffff;
  color: #111827;
  border: 1px solid #e5e7eb;
  border-bottom: none;
}

/* =========== HAVA DURUMU INLINE =========== */

.weather-inline {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 4px;
  font-size: 0.85rem;
  color: #111827;
}

.weather-main-row,
.weather-meta-row {
  display: flex;
  align-items: center;
  gap: 8px;
}

.w-icon {
  font-size: 1.2rem;
}

.w-temp {
  font-size: 1.05rem;
  font-weight: 600;
}

.w-item {
  color: #4b5563;
}

/* =========== SECTION HEADER =========== */

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;
  flex-wrap: wrap;
}

.section-header h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #111827;
}

.section-header h3 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #374151;
}

.section-actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

.section-header h2,
.section-header h3 {
  margin: 0;
}

/* =========== CARD GRID (Aƒüa√ß & Sebze 6‚Äôlƒ±) =========== */

.cards-grid {
  display: grid;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  gap: 12px;
}

@media (max-width: 1100px) {
  .cards-grid {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

@media (max-width: 800px) {
  .cards-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .app-header {
    flex-direction: column;
    align-items: stretch;
  }

  .header-right {
    align-items: flex-start;
  }
}

.tree-card {
  background: #ffffff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 12px rgba(15, 23, 42, 0.05);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}

.tree-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(15, 23, 42, 0.12);
}

.tree-card-image-wrapper {
  width: 100%;
  height: 140px;
  border-radius: 16px 16px 0 0;
  overflow: hidden;
  background: #f3f4f6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tree-card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

.tree-card-body {
  padding: 8px 10px 10px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.tree-card-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
}

.tree-card-header-row h3 {
  margin: 0;
  font-size: 0.95rem;
}

.tree-card-meta-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
}

.tree-chip {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 0.75rem;
  background: #eef2ff;
  color: #4f46e5;
}

.tree-card-note {
  margin: 4px 0 0;
  font-size: 0.8rem;
  color: #4b5563;
}

/* Aƒüa√ß / sebze kartƒ± ilerleme etiketi */
.tree-progress-pill {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 0.75rem;
  background: #e5e7eb;
  color: #111827;
}

.tree-progress-pill-done {
  background: #dcfce7;
  color: #15803d;
}

/* =========== MODAL =========== */

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
}

.modal {
  background: #ffffff;
  border-radius: 16px;
  padding: 16px 18px;
  max-width: 750px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 16px 40px rgba(15, 23, 42, 0.4);
}

.modal-form {
  max-width: 700px;
}

.modal h2 {
  margin-top: 0;
}

.modal-actions {
  display: flex;
  justify-content: space-between;
  gap: 8px;
  margin-top: 12px;
}

.modal-actions .btn.danger {
  margin-right: auto;
}

/* =========== Ay Bazlƒ± Bakƒ±m Formu =========== */

.months-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 8px;
  margin-top: 8px;
}

.month-item {
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  padding: 6px 8px;
  background: #f9fafb;
}

.month-title {
  font-size: 0.8rem;
  font-weight: 600;
  margin-bottom: 4px;
}

/* Resim √∂nizleme */

.form-image-preview {
  margin-top: 6px;
}

.form-image-preview img {
  max-width: 100%;
  max-height: 160px;
  border-radius: 12px;
  object-fit: cover;
}

/* =========== Bakƒ±m Listesi =========== */

.maintenance-list {
  list-style: none;
  padding: 0;
  margin: 8px 0 0;
}

.maintenance-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 8px;
  border-radius: 10px;
  font-size: 0.85rem;
  margin-bottom: 4px;
  background: #f9fafb;
  cursor: pointer;
  transition: background 0.12s ease, transform 0.05s ease;
}

.maintenance-item:hover {
  background: #eef2ff;
  transform: translateY(-1px);
}

.maintenance-item.important {
  border-left: 3px solid #f97316;
}

.maintenance-item.completed {
  background: #dcfce7;
  color: #166534;
}

.maintenance-month-chip {
  display: inline-flex;
  align-items: center;
  padding: 2px 6px;
  border-radius: 999px;
  font-size: 0.75rem;
  background: #e5e7eb;
  color: #374151;
}

.maintenance-task {
  flex: 1;
}

.maintenance-check {
  font-size: 1rem;
}

.maintenance-empty {
  font-size: 0.85rem;
  color: #6b7280;
}

/* Bu ay box */

.this-month-box {
  border-radius: 12px;
  padding: 8px 10px;
  background: #eef2ff;
  margin: 8px 0 10px;
  font-size: 0.88rem;
}

.this-month-box h4 {
  margin: 0 0 4px;
  font-size: 0.9rem;
}

.this-month-done {
  display: inline-flex;
  align-items: center;
  margin-left: 4px;
  font-size: 0.8rem;
  color: #16a34a;
}

/* =========== Hatƒ±rlatmalar Grid =========== */

.reminders-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 16px;
  margin-top: 16px;
}

.reminder-card-tree {
  cursor: pointer;
  transition: transform 0.08s ease, box-shadow 0.1s ease;
  padding: 16px !important;
}

.reminder-card-tree:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.16);
}

.card-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #f3f4f6;
}

.card-header-row h3 {
  margin: 0;
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
}

.badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 10px;
  border-radius: 999px;
  font-size: 0.75rem;
  font-weight: 500;
  background: #e5e7eb;
  color: #374151;
  white-space: nowrap;
}

.notify-row {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 12px;
  align-items: center;
}

/* Bakƒ±m Hatƒ±rlatmalarƒ± sayfasƒ± i√ßin √∂zel d√ºzenlemeler */
.card + h3 {
  margin-top: 32px;
  margin-bottom: 8px;
  font-size: 1.3rem;
  font-weight: 600;
  color: #111827;
}

.card + .card {
  margin-top: 20px;
}

/* Bildirim kartlarƒ± i√ßin ekstra padding */
.card .section-header + .notify-row {
  margin-top: 16px;
}

.card .notify-row + label {
  margin-top: 12px;
}

/* =========== Month Select (Modern) =========== */

.month-select {
  max-width: 180px;
  font-size: 0.9rem;
  background: #f9fafb;
}

/* =========== Takvim Sayfasƒ± =========== */

.calendar-page {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.calendar-section h2 {
  margin: 0 0 8px;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 10px;
}

@media (max-width: 900px) {
  .calendar-grid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

@media (max-width: 700px) {
  .calendar-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

.month-card {
  background: #ffffff;
  border-radius: 14px;
  padding: 8px 10px;
  box-shadow: 0 4px 12px rgba(15, 23, 42, 0.08);
  cursor: pointer;
  font-size: 0.85rem;
  display: flex;
  flex-direction: column;
  gap: 4px;
  transition: transform 0.08s ease, box-shadow 0.08s ease;
}

.month-card:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 18px rgba(15, 23, 42, 0.14);
}

.month-card-empty {
  background: #f9fafb;
  color: #6b7280;
}

.month-card-current {
  border: 1px solid #4f46e5;
}

.month-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 6px;
}

.month-card-name {
  font-weight: 600;
}

.month-card-badge {
  font-size: 0.7rem;
  padding: 2px 6px;
  border-radius: 999px;
  background: #eef2ff;
  color: #4338ca;
}

.month-card-text {
  margin: 0;
}

.month-card-progress-bar {
  width: 100%;
  height: 6px;
  border-radius: 999px;
  background: #e5e7eb;
  overflow: hidden;
}

.month-card-progress-fill {
  height: 100%;
  border-radius: 999px;
  background: linear-gradient(90deg, #22c55e, #4ade80);
}

.month-card-percent {
  margin: 0;
  font-size: 0.8rem;
  color: #166534;
}

/* =========== Home Sayfasƒ± =========== */

.home-page {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.home-header-row {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.home-card {
  flex: 1;
  min-width: 240px;
  border-radius: 14px;
  background: #ffffff;
  box-shadow: 0 4px 14px rgba(15, 23, 42, 0.08);
  padding: 10px 12px;
}

.home-card h2,
.home-card h3 {
  margin: 0 0 6px;
}

.home-date-line {
  margin: 0 0 12px;
  font-size: 0.9rem;
}

/* Bug√ºn√ºn √∂zeti grid */
.today-summary-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-top: 12px;
}

.summary-item {
  background: #f9fafb;
  border-radius: 8px;
  padding: 10px;
  border: 1px solid #e5e7eb;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.summary-label {
  font-size: 0.7rem;
  color: #6b7280;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.summary-value {
  font-size: 1rem;
  font-weight: 700;
  color: #1f2937;
}

.summary-tip {
  font-size: 0.75rem;
  color: #10b981;
  font-weight: 500;
}

/* Sezon ba≈ülangƒ±√ß uyarƒ± kutusu */
.season-alert-box {
  margin-top: 12px;
  background: linear-gradient(135deg, #dbeafe 0%, #bfdbfe 100%);
  border: 2px solid #3b82f6;
  border-radius: 10px;
  padding: 10px;
}

.season-alert-header {
  font-size: 0.85rem;
  font-weight: 700;
  color: #1e40af;
  margin-bottom: 8px;
}

.season-alert-item {
  background: rgba(255, 255, 255, 0.7);
  padding: 8px;
  border-radius: 6px;
  font-size: 0.8rem;
  color: #1e3a8a;
  margin-bottom: 6px;
}

.season-alert-item:last-child {
  margin-bottom: 0;
}

/* Dark mode i√ßin sezon uyarƒ±larƒ± */
body.theme-dark .season-alert-box {
  background: linear-gradient(135deg, #1e3a8a 0%, #1e40af 100%);
  border-color: #3b82f6;
}

body.theme-dark .season-alert-header {
  color: #93c5fd;
}

body.theme-dark .season-alert-item {
  background: rgba(0, 0, 0, 0.3);
  color: #bfdbfe;
}

/* Acil g√∂revler kutusu - Temel stil */
.urgent-tasks-box {
  margin-top: 12px;
  border-radius: 10px;
  padding: 10px;
  transition: all 0.3s ease;
}

/* Acil g√∂revler var - Sarƒ±/Turuncu */
.urgent-tasks-box.urgent {
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  border: 2px solid #f59e0b;
}

.urgent-tasks-box.urgent .urgent-header {
  color: #92400e;
}

.urgent-tasks-box.urgent .urgent-type {
  background: #f59e0b;
}

.urgent-tasks-box.urgent .urgent-name {
  color: #78350f;
}

.urgent-tasks-box.urgent .urgent-task-detail {
  color: #92400e;
}

.urgent-tasks-box.urgent .urgent-more {
  color: #92400e;
}

/* G√∂rev yok - Ye≈üil/Normal */
.urgent-tasks-box.normal {
  background: linear-gradient(135deg, #d1fae5 0%, #a7f3d0 100%);
  border: 2px solid #10b981;
}

.urgent-tasks-box.normal .urgent-header {
  color: #065f46;
}

.urgent-success-message {
  text-align: center;
  color: #065f46;
  font-size: 0.85rem;
  padding: 8px;
  font-weight: 500;
}

.urgent-header {
  font-size: 0.85rem;
  font-weight: 700;
  margin-bottom: 8px;
}

.urgent-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.urgent-item {
  background: rgba(255, 255, 255, 0.7);
  padding: 8px 10px;
  border-radius: 8px;
  font-size: 0.8rem;
}

.urgent-item-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 4px;
}

.urgent-type {
  color: white;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: 600;
}

.urgent-name {
  font-weight: 700;
  font-size: 0.85rem;
}

.urgent-task-detail {
  font-size: 0.75rem;
  line-height: 1.4;
  padding-left: 8px;
  border-left: 2px solid rgba(0, 0, 0, 0.1);
  margin-top: 4px;
}

.urgent-more {
  text-align: center;
  font-size: 0.75rem;
  font-style: italic;
  margin-top: 4px;
}

/* Dark mode i√ßin bug√ºn√ºn √∂zeti */
body.theme-dark .summary-item {
  background: #374151;
  border-color: #4b5563;
}

body.theme-dark .summary-label {
  color: #9ca3af;
}

body.theme-dark .summary-value {
  color: #e5e7eb;
}

body.theme-dark .summary-tip {
  color: #6ee7b7;
}

/* Dark mode - Acil g√∂revler var */
body.theme-dark .urgent-tasks-box.urgent {
  background: linear-gradient(135deg, #451a03 0%, #78350f 100%);
  border-color: #f59e0b;
}

body.theme-dark .urgent-tasks-box.urgent .urgent-header {
  color: #fde68a;
}

body.theme-dark .urgent-tasks-box.urgent .urgent-name {
  color: #fde68a;
}

body.theme-dark .urgent-tasks-box.urgent .urgent-task-detail {
  color: #fcd34d;
  border-left-color: rgba(253, 230, 138, 0.3);
}

body.theme-dark .urgent-tasks-box.urgent .urgent-more {
  color: #fcd34d;
}

/* Dark mode - G√∂rev yok */
body.theme-dark .urgent-tasks-box.normal {
  background: linear-gradient(135deg, #064e3b 0%, #065f46 100%);
  border-color: #10b981;
}

body.theme-dark .urgent-tasks-box.normal .urgent-header {
  color: #6ee7b7;
}

body.theme-dark .urgent-success-message {
  color: #6ee7b7;
}

body.theme-dark .urgent-item {
  background: rgba(0, 0, 0, 0.3);
}

/* Mobil i√ßin √∂zet grid */
@media (max-width: 600px) {
  .today-summary-grid {
    grid-template-columns: 1fr;
  }
}

.home-stats-list {
  list-style: none;
  margin: 4px 0 0;
  padding: 0;
  font-size: 0.9rem;
}

/* Bah√ße adƒ± g√∂sterimi */
.garden-name-display {
  font-size: 1.2rem;
  font-weight: 600;
  color: #10b981;
  margin: 12px 0 16px 0;
  padding: 8px 12px;
  background: linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%);
  border-radius: 10px;
  border-left: 4px solid #10b981;
}

/* Bah√ße istatistik grid */
