/* Toggle switch */
.switch {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 22px;
  font-size: 0; /* i√ß bo≈üluk yazƒ± vs. olmasƒ±n */
}

.settings-item > .switch {
  flex-shrink: 0; /* saƒüdaki switch ta≈ümasƒ±n, esnemesin */
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background-color: #e5e7eb;
  transition: 0.2s;
  border-radius: 999px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  background-color: #ffffff;
  transition: 0.2s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: #4f46e5;
}

.switch input:checked + .slider:before {
  transform: translateX(20px);
}

